import React, { useState } from "react";
import { MuuriComponent } from "muuri-react";
import { Header, Demo, Footer, Button, ResizableWrapper,ResizableWrapper2 } from "./myComponent";




const CustomMuuriGrid = ()=>{


  const initialItems = (no_item) =>{
    const items = [];
    for (let i = 0; i < no_item; i++) {
  
        items.push( {id:i,color:"blue",width:200,height:200,text:'drag me!'});
    }
    return items
    
  }
  
  
  const addItem = ()=> {
    
        setItems([...items, {id:itemsCount,color:"blue",width:200,height:200,text:'drag me!'}])
        setItemsCount(itemsCount+1)
  }
      
  const removeItem = (id) => {
    alert('');
     // setItems(items.filter(item => item.id !== id))
      
  }

    const [items, setItems] = useState(initialItems(10));
    const [itemsCount, setItemsCount] = useState(10);

    // Children.
    const children = items.map((item) => (
      <Item
          key={item.id}
          color={item.color}
          id={item.id}
          width={item.width}
          height={item.height}
          text={item.text}
          remove = {removeItem(item.id)}
        />
    ));


        return <Demo>
                    <Header />
                    <MuuriComponent
                        dragEnabled
                        dragStartPredicate={{ handle: ".content-header" }}
                    >
                    {children}
                    </MuuriComponent>
        
                    <Footer>
                    <Button onClick={addItem} />
                    </Footer>
                </Demo>
    

}



// Item component.
const Item= ResizableWrapper2();
     



const AddCard = (props)=>{

    var div_style={
        background: '#fff',
        padding:10,
        
    }

    var str =<><br/>
        <div style={div_style}>
            <h5>Add New Card</h5>
            <hr/>
          <form onSubmit={props.onAddItemHandler}>
                <div class="form-group">
                    <label for="">Text of card</label>
                    <input type="text" class="form-control" name="box_content" placeholder="Enter box text.." onChange={props.onFormFieldChangeHandler} required/>
                 </div>
                 <div class="form-group">
                    <label for="">Y Axis of card (Row)</label>
                    <input type="number" class="form-control" name="box_row" placeholder="Enter y axis (Row)"  onChange={props.onFormFieldChangeHandler} min="0" required/>
                 </div>
                 <div class="form-group">
                    <label for="">X Axis of card (Column)</label>
                    <input type="number" class="form-control" name="box_col" placeholder="Enter x axis (Col)"  onChange={props.onFormFieldChangeHandler} min="0" max="12" required/>
                 </div>
                 <div class="form-group">
                    <label for="">Width of card</label>
                    <input type="number" class="form-control" name="box_width" placeholder="Enter width"  onChange={props.onFormFieldChangeHandler} min="0" max="12" required/>
                 </div>
                 <div class="form-group">
                    <label for="">Height of card </label>
                    <input type="number" class="form-control" name="box_height" placeholder="Enter height"  onChange={props.onFormFieldChangeHandler} min="0" max="6" required/>
                 </div>
                
                <button type="submit" class="btn btn-primary btn-block">Add Crad !</button>
            </form>
        </div>
        </>

    return str

}



  



  
export default CustomMuuriGrid;