/* React */
import React, { useState } from "react";
import ReactDom from "react-dom";
/* Muuri-react */
import { MuuriComponent } from "muuri-react";
/* Utils & components */
import { Header, Demo, Footer, Button, ResizableWrapper } from "./myComponent";
import { generateItems } from "./utils";
/* Style */


// App.
const MyMuuriGrid = () => {
  const [items, setItems] = useState(generateItems());

  // Children.
  const children = items.map(({ id, color }) => (
    <Item 
      key={id} 
      color={color} 
      
    />
  ));

  return (
    <Demo>
      {/* Header */}
      <Header />
      {/* Content */}
      <MuuriComponent
        dragEnabled
        dragStartPredicate={{ handle: ".content-header" }}
      >
        {children}
      </MuuriComponent>
      {/* Footer */}
      <Footer>
        <Button onClick={() => setItems(items.concat(generateItems()))} />
      </Footer>
    </Demo>
  );
};

// Item component.
const Item = ResizableWrapper(
  ({ color,id }) => (
    <div className={`content ${color}`}>
      <div className="content-header" />
    </div>
  ),
  {
    width: 200,
    height: 100
  }
);

export default MyMuuriGrid;
