import React from "react";
import { MuuriComponent } from "muuri-react";
import { Header, Demo, Footer, Button, ResizableWrapper,ResizableWrapper2 } from "./myComponent";


class NewGridMuuri extends React.Component{
    constructor(){
        super();
        this.state={
                items:initialItems(10),
                itemsCount:10,
                box_title:'',
                box_content:'',
                box_loc:'',
                box_width:'',
                box_height:'',
                box_color:'orange',
                confirm_id:0,
                box_color_filter:'all',
                box_title_filter:'',
        }

 
        this.addItem = this.addItem.bind(this)

        this.onFormFieldChangeHandler = this.onFormFieldChangeHandler.bind(this)
        this.onAddItemHandler = this.onAddItemHandler.bind(this)
        this.onRemoveItemHandler = this.onRemoveItemHandler.bind(this)
        this.onFilterItemHandler = this.onFilterItemHandler.bind(this)
        this.updateFilterReference =this.updateFilterReference.bind(this)
    }

    addItem = ()=> {
    
        this.setState({
            items: [...this.state.items, {id:this.state.itemsCount,color:"blue",width:200,height:200,text:'drag me!'}]
        })
            
        this.setState({itemsCount:this.state.itemsCount+1})
        
    }

    onFormFieldChangeHandler = (event)=>{
        
        const target = event.target;
        this.setState({
			[target.name]:target.value
        })
        
    }

    onAddItemHandler = (event)=>{

        if(this.state.box_content !=='' && this.state.box_width !=='' && this.state.box_height !=='')
        {
            var title = this.state.box_title
            const content = this.state.box_content
            const width = parseInt(this.state.box_width)
            const height = parseInt(this.state.box_height)
            const color = this.state.box_color
            var loc = this.state.itemsCount
            if(this.state.box_loc!=""){
                 var box_loc = parseInt(this.state.box_loc)-1
                 loc= box_loc
                 
            }
            if(title==""){
                title="Title "+(this.state.itemsCount+1)
            }
            const newData = this.state.items.slice(0);
            newData.splice(loc, 0, {id:this.state.itemsCount,color:color,width:width,height:height,text:content,title:title,animation_class:''});
           
            this.setState({
                //items: [...this.state.items,{id:this.state.itemsCount,color:"blue",width:width,height:height,text:content}],
                items:newData,
                itemsCount:this.state.itemsCount+1,
        
            })

        }else{

            alert('Error');
        }

        event.preventDefault();

    }

    onRemoveItemHandler = (event)=>{
      
        const arr = this.state.items;
        var item_index=-1;
        for(var i = 0; i < arr.length; i++) {
            if(arr[i]['id'] === this.state.confirm_id) {
                item_index = i;
            }
        }

        arr[item_index]={id:arr[item_index]['id'],color:arr[item_index]['color'],width:arr[item_index]['width'],height:arr[item_index]['height'],text:arr[item_index]['text'],animation_class:'animate__animated animate__bounceOut'}

       
        this.setState({
            items:arr
        })
        
        setTimeout(() => {
           
            this.setState({items:this.state.items.filter(it => it.id !== this.state.confirm_id)})

        }, 800);

    
    }


    onFilterItemHandler = (event)=>{
       
        const target = event.target;
        this.setState({
            [target.name]:target.value
           
        })
         
        this.updateFilterReference()
    }


    filter= (props)=>{

        const color_filter = this.state.box_color_filter.toLowerCase();
        const title_filter = this.state.box_title_filter;

        var isFilterMatch = color_filter === "all" ? true : props.color === color_filter;

        var isSearchMatch = !title_filter ? true : props.title.toLowerCase().indexOf(title_filter) > -1;

         return isFilterMatch && isSearchMatch
    }

    updateFilterReference() {
        
        this.filterReference = (props) => this.filter(props);
      }
    
   
    render(){

        // Children.
        const children = this.state.items.map((item,index) => (
            <Item
                key={item.id}
                color={item.color}
                id={item.id}
                width={item.width}
                height={item.height}
                text={item.text}
                title={item.title}
                loc={index}
                animation_class={item.animation_class}
                remove = {() => this.setState({confirm_id:item.id})}
                
            />
        ));
  

        return <><div className="container-fluid">
                    <div className="row">
                        <div className="col-2" style={{borderRight:'1px solid #eee'}}>
                        
                            <AddItemOnLocation onFormFieldChangeHandler={this.onFormFieldChangeHandler} onAddItemHandler={this.onAddItemHandler} av_item= {this.state.items.length+1}/>
                           {/* <hr/>
                            <Button onClick={this.addItem} />
                           */}
                                
                        </div>
                        
                        <div className="col-10">
                            <div className="row" style={{padding:18,borderBottom:'1px solid #eee'}}>
                                <label>FILTER &nbsp;&nbsp; | &nbsp;&nbsp;</label> 

                                 <label > Title : &nbsp;</label> <input class="form-control col-md-2"  name="box_title_filter" type="search" placeholder="Search" aria-label="Search" onChange={this.onFilterItemHandler}/>
                                   
                                
                                    <label >
                                    &nbsp;&nbsp; | &nbsp;&nbsp; Background Color : &nbsp;
                                    </label>
                                    <select class="form-control col-md-2" name="box_color_filter" onChange={this.onFilterItemHandler}>
                                        <option value ="all">All</option>
                                        <option value ="orange">Orange</option>
                                        <option value ="green">Green</option>
                                        <option value ="blue">Blue</option>
                                        <option value ="red">Red</option>
                                        <option value ="white">White</option>
                                        <option value ="black">Black</option>
                                    </select>
                                    
                                   
                            </div>
                        
                            <Demo>
                              
                                <MuuriComponent
                                    id={'muuri_id'}
                                    dragEnabled
                                    dragStartPredicate={{ handle: ".content-header" }}
                                    propsToData={({color,title}) => ({color,title})}
                                    filter={this.filterReference}
                                >
                                {children}
                                </MuuriComponent>

                                
                            </Demo>

                        </div>   
                    </div>
                </div>
                <div className="modal" id="exampleModal" tabIndex="-1" role="dialog">
                        <div className="modal-dialog modal-dialog-centered " role="document">
                            <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">Confirm Deletion</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <p>Are you sure you want to remove this item ?</p>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">No</button>
                                &nbsp;&nbsp;<button type="button" className="btn btn-danger" data-dismiss="modal" onClick={this.onRemoveItemHandler}>Yes</button>
                            </div>
                            </div>
                        </div>
                    </div>
                </>

    }


}

// Item component.
const Item= ResizableWrapper2();

function oneOf(array) {
    return array[Math.floor(Math.random() * Math.floor(array.length))];
  }

const initialItems = (no_item) =>{
    const item_arr = [];
    for (let i = 0; i < no_item; i++) {
        const color = oneOf(["orange", "green", "blue","white","red","black"]);
        item_arr.push( {id:i,color:color,width:195,height:195,text:'Content !!', title:'Title '+(i+1),animation_class:''});
    }
    return item_arr
    
  }
  


  const AddItemOnLocation = (props)=>{

    var div_style={
        background: '#fff',
        padding:10,
        
    }
  
    var str =<><br/>
        <div style={div_style}>
            <h5>Add New Card</h5>
            <hr/>
          <form onSubmit={props.onAddItemHandler}>
              
                <div className="form-group">
                    <label >Title</label>
                    <input type="text" className="form-control" name="box_title" placeholder="Title" onChange={props.onFormFieldChangeHandler} />
                 </div>
                <div className="form-group">
                    <label >Content</label>
                    <input type="text" className="form-control" name="box_content" placeholder="Content" onChange={props.onFormFieldChangeHandler} required/>
                 </div>
                
                 <div className="form-group">
                    <label >Width (px)</label>
                    <input type="number" className="form-control" name="box_width" placeholder="Width"  onChange={props.onFormFieldChangeHandler} min="100" max="900" required/>
                 </div>
                 <div className="form-group">
                    <label >Height (px)</label>
                    <input type="number" className="form-control" name="box_height" placeholder="Height"  onChange={props.onFormFieldChangeHandler} min="100" max="800" required/>
                 </div>

                 <div className="form-group">
                    <label>Position</label>&nbsp;<span tooltip={`Add card at a specific position between 1 to ${props.av_item}. Default position is next to last card `} flow="up"><i className="fa fa-info-circle" aria-hidden="true"></i></span>
                    <input type="number" className="form-control" name="box_loc" placeholder="Position"  onChange={props.onFormFieldChangeHandler} min="1" />
                 </div>

                 <div className="form-group">
                        <label >Background Color</label>
                        <select className="form-control" id="box_color" name="box_color" onChange={props.onFormFieldChangeHandler}>
                            <option value ="orange">Orange</option>
                            <option value ="green">Green</option>
                            <option value ="blue">Blue</option>
                            <option value ="red">Red</option>
                            <option value ="white">White</option>
                            <option value ="black">Black</option>
                        </select>
                </div>
                
                <button type="submit" className="btn btn-primary btn-block">Add Crad !</button>
            </form>
        </div>
        </>
  
    return str
  
  }
  
  

  


export default NewGridMuuri;